import React, { useEffect, useState } from 'react';

import { Box, Grid, Slider, Typography, Input } from '@material-ui/core';

const CustomizedSlider = ({ sectionTitle, num, resetNum }) => {
  const [value, setValue] = useState(30);

  useEffect(() => {
    setValue(30)
  }, [resetNum])
  
  
  const handleSliderChange = (event, newValue) => {
    setValue(newValue);
    num(newValue)
  };

  const handleInputChange = (event) => {
    setValue(event.target.value === '' ? '' : Number(event.target.value));
    num(event.target.value)

  };

  const handleBlur = () => {
    if (value < 0) {
      setValue(0);
    } else if (value > 100) {
      setValue(100);
    }
  };

  return (
    <div style={{display:'flex', alignItems: 'center', alignSelf:'start', width:'100%', padding:'35px 0 20px'}}>
      <Typography id="input-slider" gutterBottom style={{marginRight:'15px', color:'#505050'}}>
        {sectionTitle}
      </Typography>

        <Box sx={{ width: '71%'}} >
        <Grid container spacing={2} alignItems="center">
          <Grid item xs>
            <Slider
              value={typeof value === 'number' ? value : 0}
              onChange={handleSliderChange}
              aria-labelledby="input-slider"
            />
          </Grid>
          <Grid item>
            <Input
              value={value}
              size="small"
              onChange={handleInputChange}
              onBlur={handleBlur}
              inputProps={{
                step: 10,
                min: 0,
                max: 100,
                type: 'number',
                'aria-labelledby': 'input-slider',
              }}
            />
          </Grid>
        </Grid>
      </Box>
    </div>
  );
}

export default CustomizedSlider;