import React from 'react';

import { Button, FormControl, FormHelperText, InputLabel } from '@material-ui/core'

const DownloadButtons = ({ fileToDownload, formats }) => {

    const download = ( format ) => {
        if (!fileToDownload) {
          alert('Please Generate a Meme!')
        } else {
          var link = document.createElement('a');
          link.download = `memeLand.${format}`;
          link.href = document.getElementById('myCanvas').toDataURL()
          link.click();
        }
      };

  return (
    <>
    {!fileToDownload ?
        <Button variant="outlined" color="secondary" size="large" onClick={()=> download()} fullWidth>Download</Button>
        :
        <FormControl>
          <InputLabel htmlFor="my-input"></InputLabel>
          <FormHelperText id="my-helper-text" style={{textAlign: 'center'}}>Download</FormHelperText>
          <div id="my-input" aria-describedby="my-helper-text" >
          {formats.map((format, key) =>
            <Button key={key} variant="contained" size="small" onClick={()=> download(format)} style={{margin:'5px', backgroundColor:'#4cd964', color:'white'}}>{format}</Button>
          )}  
          </div>
        </FormControl>
      }
    </>
  )
}

export default DownloadButtons