import React from 'react';
import { useEffect } from 'react';

import defaultImg from '../../../assets/img.jpg';

import useStyles from './styles';

const Meme = ({ memes, templateImage }) => {
  
  const classes = useStyles();

  const meme = memes.map(meme => meme)[0]

  useEffect(() => {

    var canvas = document.getElementById("myCanvas");
    var ctx = canvas.getContext("2d");

    var img = new Image();
    img.setAttribute('crossOrigin', 'anonymous');

    const updateCanvas = () => {
      ctx.font = `${meme.fontSize} ${meme.fontFamily}`
      ctx.fillStyle = meme.fontColor 
      ctx.fillText(meme.topText, 30, 80)
      ctx.fillText(meme.bottomText, 30, 290)
    }

    const resetCanvas = () => {
      ctx.font = ''
      ctx.fillStyle = ''
      ctx.fillText('', 30, 80)
      ctx.fillText('', 30, 290)
    }
    
    const noImageCanvas = () => {
      ctx.font = '60px impact'
      ctx.fillStyle = 'red'
      ctx.fillText('', 30, 80)
      ctx.fillText('select an image', 30, 290)
    }

    img.onload = () => {
      var scale = Math.max(canvas.width / img.width, canvas.height / img.height);
      var x = (canvas.width / 2) - (img.width / 2) * scale;
      var y = (canvas.height / 2) - (img.height / 2) * scale;
      ctx.drawImage(img, x, y, img.width * scale, img.height * scale);

      templateImage ? resetCanvas() : meme.selectedFile ? updateCanvas()  : noImageCanvas();

    }

    img.src = templateImage ? templateImage : meme.selectedFile ? meme.selectedFile : defaultImg;

  }, [meme, templateImage]);

  return (
    <div className={classes.meme}>
      <canvas id="myCanvas" width='450' height='360' />
    </div>
  );
};

export default Meme;