import { Button, Typography } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { SketchPicker } from 'react-color';

import useStyles from './styles';

const ColorPicker = ({ sectionTitle, pickedColor, resetColor }) => {

    const classes = useStyles();

    const [ colorToPick, setColorToPick ] = useState(`rgba(0,183,255, 1)`) 
    const [ displayColorPicker, setDisplayColorPicker ] = useState(false);

    const handleColor = (color) => {
        setColorToPick(`rgba(${ color.rgb.r }, ${ color.rgb.g }, ${ color.rgb.b }, ${ color.rgb.a })`)
        pickedColor(`rgba(${ color.rgb.r }, ${ color.rgb.g }, ${ color.rgb.b }, ${ color.rgb.a })`)
    };
    const handleClick = () => {    
        setDisplayColorPicker(!displayColorPicker);
    };
    const handleClose = () => {
        setDisplayColorPicker(false);
    }

    useEffect(() => {
        setColorToPick(`rgba(240,250,255, 1)`)

    }, [resetColor])
    
    return (
        <div style={{display:'flex', alignItems: 'center', alignSelf:'start'}}>
        <Typography style={{marginRight:'15px', color:'#505050'}}>{sectionTitle}</Typography>
        <Button className={classes.colorPicker} onClick={handleClick} style={{backgroundColor:`${colorToPick}`}}>
            { displayColorPicker ? 
                <div style={{position: 'absolute', zIndex: '2',}}>
                <div style={{position: 'fixed', top: '0px', right: '0px', bottom: '0px', left: '0px'}} onClick={handleClose} />
                <SketchPicker color={colorToPick} onChange={handleColor} />
                </div> : 
                null 
            }
        </Button>
        </div>
        
    )
}

export default ColorPicker