import React, { useEffect } from 'react'
import { FormControl, InputLabel, MenuItem, Select } from '@material-ui/core'
import { useState } from 'react'

const CustomizedSelect = ({ sectionTitle, itemsArray, selectedItem, resetItem }) => {

    const [ fontToSelect, setFontToSelect ] = useState('');

    useEffect(() => {
        setFontToSelect('')
    }, [resetItem])
    
    const handleFontFamily = e => {
        setFontToSelect(e.target.value)
        selectedItem(e.target.value)
    }

    return (
        <FormControl fullWidth variant="standard">
            <InputLabel id="font-family-picker" >{sectionTitle}</InputLabel>
            <Select
            labelId="font-family-picker"
            id="font-family"
            name="fontFamily" 
            value={fontToSelect}
            label="Font Family"
            onChange={handleFontFamily}
            >
            {itemsArray.map((item, key) => 
                <MenuItem key={key} value={item}>{item}</MenuItem>
            )}
            </Select>
        </FormControl>  
    )
}

export default CustomizedSelect